import {
    getGoodSecondCateService,
    addGoodsService,
    getSkuTypeNumService,
    getSearchKeywordService
} from "../../services/goodsService";
import {mapActions, mapState} from "vuex";
import {Toast} from "vant";
import SpeechRecognizerDialog from "../../common/currency/SpeechRecognizerDialog";
import Vue from 'vue';
import {store} from "../../utils/storage";
export const moreClassify ={
    name: "moreClassify",
    data() {
        return {
            isShowVoiceBtn:false, //语音搜索入口
            clientHeight:0,
            tabBarHeight:0,
            goodsListHeight:0,
            skuTypeNum:0, //购物车种类数量
            goodsCateIndex:0, // 一级分类下标
            goodsCateList:[], // 一级分类数据列表
            secondCateList:[], // 二级分类数据列表
            secondCateIndex:0, // 二级分类下标
            goodsListData: [],//商品集合

            loading: false, //停止加载
            finished: false,//没有更多
            offsetHeight:600,
            sortMethod:"", //  1 从小到大 2从大到小 3 销量
            isPriceSort:false, // false 从小到大 true 从大到小

            tabsScrollLeft:"",
            goodsListTop:"",
            classifyScrollTop:"",
            icon:{
                filter:require("@/assets/images/mall/icon_filter.png"),
                filterDown:require("@/assets/images/mall/icon_filter_down.png"),
                filterUp:require("@/assets/images/mall/icon_filter_up.png"),
            },
            defaultImg: 'this.src="' + require('../../assets/images/default/default_picture.png') + '"',
            sendVal:false,
            recognizeResult:"正在聆听",

            goodsFirstHttp:false,
            secondCateFirstHttp:false,
            startPoint:0,
            rows:10,
            changeInfoRows:0,
            isShowCircle:false,
            addBtnIcon:null,
            isloading:false,
            addGoodsLoading:false,

            showloading:null,
            defaultWord: '',
            searchKeyword: '',
        }
    },
    components: {
        'dialog-bar' : SpeechRecognizerDialog
    },
    watch: {
        sortMethod:function (val) {
            if (val=="") {
                return;
            }
            this.$store.commit("moreGoodsPageNumberChange", {
                custPageNum: true,
                pageNumber: 1
            });
            this.getMoreGoods(this.pageNumber);
        },
        finished:function (val) {
            if (val){
                this.$nextTick(()=>{
                    this.goodsListHeight = this.tabBarHeight - (this.$refs.goodsListTitle.clientHeight+1) - this.$refs.finishedBox.clientHeight;
                })
            }
        }
    },
    computed: mapState({
        pageNumber: state => state.productPage.moreGoodsPageNumber,
    }),
    filters:{
        threshold(data){
            return data >= 10000 ?  (data/10000>=10?'9万+':Math.floor(data / 10000) + '万+') : data
        },
        priceFilters(data){
            return data >= 10000 ? Math.floor(data/10000*100)/100+ '万' : data
        }
    },
    created() {

    },
    mounted() {
        this.$nextTick(()=>{
            let bodyHeight = document.documentElement.clientHeight || document.body.clientHeight;
            this.clientHeight = bodyHeight-this.$refs.flexFull.clientHeight;
            this.tabBarHeight = this.clientHeight-this.$refs.tabBarHeight.clientHeight;
            // this.goodsListHeight = this.tabBarHeight - this.$refs.goodsListTitle.clientHeight - this.$refs.finishedBox.clientHeight;
            console.log(this.$refs.finishedBox.clientHeight)
        })
    },
    activated(){
        if(this.$route.params.goodsCateList){
            this.clearData();
            this.goodsCateIndex = this.$route.params.goodsCateIndex;
            this.goodsCateList = this.$route.params.goodsCateList;
            this.$nextTick(()=>{
                this.calculateClassMoreList(this.goodsCateIndex);
            })
            this.getGoodSecondCate();
        }else{
            this.$refs.tabBarScroll.scrollLeft = this.tabsScrollLeft;
            this.$refs.goodsListBox.scrollTop = this.goodsListTop;
            this.$refs.classifyScroll.scrollTop = this.classifyScrollTop;
            this.changeInfoRows = this.goodsListData.length;
            this.getNewGoodsListData()
        }
        this.getSkuTypeNum(); //获取购物车种类数量
        this.getSearchKeyword();
    },
    methods: {
        ...mapActions(["getGoodsForCate","getGoodsForNewCate","getGoodsForCatePageStore"]),
        // 进入前状态
        beforeEnter(el){
            console.log( this.addBtnIcon.getBoundingClientRect())
            el.style.left = this.addBtnIcon.getBoundingClientRect().left+'px';
            el.style.top = this.addBtnIcon.getBoundingClientRect().top+'px';
            el.style.transform = 'scale(1)'
        },
        // 进入中
        enter(el, done){
            // el.offsetWidth 强制html渲染动画
            // el.offsetWidth 这句话如何不写就不会有动画效果直接渲染的
            el.offsetWidth;
            el.style.left = this.$refs.cartIcon.getBoundingClientRect().left+'px'
            el.style.top =this.$refs.cartIcon.getBoundingClientRect().top+'px'
            el.style.transform = 'scale(.5)'
            done()
        },
        afterEnter(el){
            this.isShowCircle = !this.isShowCircle
        },
        //语音搜索
        goToSpeech(){
            var _this = this;
            var ifly = cordova.plugins.IflyForXwjPlugin;
            _this.recognizeResult = "正在聆听";
            const platform = navigator.userAgent;
            const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
            if (isiOS) {
                _this.sendVal=true;
                ifly.startListen(function (result) {
                        if (result){
                            var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g,"");
                            _this.recognizeResult = resultcatch;
                            setTimeout(function () {
                                _this.sendVal = false;
                                console.log(resultcatch);
                                _this.goToGoodsSearch(resultcatch);
                            },"2000");
                        }else {
                            _this.sendVal = false;
                            console.log('没有值')
                            Toast('我没听清您说什么')
                        }
                    },
                    function (message) {
                        _this.sendVal = false;
                        console.log("err" + message);
                    },false,true
                );
            }else {
                var list = [
                    'android.permission.RECORD_AUDIO'
                ];
                var permissions = cordova.plugins.permissions;
                permissions.requestPermissions(list,function (status) {
                    if(status.hasPermission){
                        _this.sendVal=true;
                        ifly.startListen(function (result) {
                                if (result){
                                    console.log(result);
                                    var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g,"");
                                    _this.recognizeResult = resultcatch;
                                    setTimeout(function () {
                                        _this.sendVal = false;
                                        _this.goToGoodsSearch(resultcatch);
                                    },"2000");
                                }else {
                                    _this.sendVal = false;
                                    console.log('没有值')
                                    Toast('我没听清您说什么')
                                }
                            },
                            function (message) {
                                _this.sendVal = false;
                                console.log("err" + message);
                            },false,true
                        );
                    }
                },function (status) {
                    alert("请打开麦克风权限");
                });
            }
        },

        handleTouchStart: function(e){
            this.startPoint = e.touches[0].pageY;
        },

        handleTouchMove: function(e) {
            let poChange = e.changedTouches[0].pageY - this.startPoint;
            // this.pointChange = poChange
            // console.log(poChange)
        },
        handleTouchEnd: function(e) {
            let goodsListTop = this.$refs.goodsListBox.scrollTop;
            let goodsBantListHeight = this.$refs.goodsBantList.$el.scrollHeight;
            let end = e.changedTouches[0].pageY;
            let start = this.startPoint;
            let delta = start - end;
            console.log(goodsListTop+this.tabBarHeight+10,goodsBantListHeight);
            if((goodsListTop+this.tabBarHeight+10>=goodsBantListHeight&&this.finished)||(this.goodsListData.length==0&&this.goodsFirstHttp)){
                if (delta > 150) {
                    if(this.secondCateIndex!=this.secondCateList.length-1){
                        this.showloading = store.get("showLoadingView");
                        if (this.showloading) {
                            this.showloading(true);
                        }
                        setTimeout(()=>{
                            if (this.showloading) {
                                this.showloading(false);
                            }
                            this.secondCateIndex = this.secondCateIndex+1;
                            if(this.secondCateIndex<=3){
                                console.log("qiansan")
                                this.$refs.classifyScroll.scrollTop = 0
                            }else if(this.secondCateIndex>=3&&this.secondCateIndex<=this.secondCateList.length-6){
                                console.log("zhongjian")
                                this.$refs.classifyScroll.scrollTop = (this.secondCateIndex-2)*60;
                            }else if(this.secondCateIndex>=this.secondCateList.length-6){
                                console.log("hou6")
                                this.$refs.classifyScroll.scrollTop = this.$refs.classifyScroll.clientHeight;
                            }
                            this.changeSecondMoreList(this.secondCateIndex);
                        },1000)
                    }
                }
            }
            if(goodsListTop <= 0){
                if (delta < -150) {
                    this.showloading = store.get("showLoadingView");
                    if (this.showloading) {
                        this.showloading(true);
                    }
                    setTimeout(()=>{
                        if (this.showloading) {
                            this.showloading(false);
                        }
                        if(this.secondCateIndex!=0){
                            this.secondCateIndex = this.secondCateIndex-1;
                            if(this.secondCateIndex<=3){
                                console.log("qiansan")
                                this.$refs.classifyScroll.scrollTop = 0
                            }else if(this.secondCateIndex>=3&&this.secondCateIndex<=this.secondCateList.length-6){
                                console.log("zhongjian")
                                this.$refs.classifyScroll.scrollTop = (this.secondCateIndex-2)*60;
                            }else if(this.secondCateIndex>=this.secondCateList.length-6){
                                console.log("hou6")
                                this.$refs.classifyScroll.scrollTop = this.$refs.classifyScroll.clientHeight;
                            }
                            this.changeSecondMoreList(this.secondCateIndex);
                        }

                    },1000)
                }
            }

        },
        //点击一级分类标签
        changeMoreList(index) {
            this.goodsCateIndex = index;
            this.calculateClassMoreList(index);
            this.$refs.goodsListBox.scrollTop = 0;
            this.$refs.classifyScroll.scrollTop = 0;
            this.sortMethod = "";
            this.isPriceSort = false;
            this.secondCateFirstHttp = false;
            this.getGoodSecondCate();

        },
        //计算分类滑动宽度
        calculateClassMoreList(index){
            if(index<4){
                this.$refs.tabBarScroll.scrollLeft = 0;
            }else if(index>this.goodsCateList.length-5){
                this.$refs.tabBarScroll.scrollLeft = this.$refs.tabBarItem[0].clientWidth*this.goodsCateList.length;
            }else{
                this.$refs.tabBarScroll.scrollLeft = this.$refs.tabBarItem[0].clientWidth*index-(this.$refs.tabBarScroll.offsetWidth/2)+this.$refs.tabBarItem[0].clientWidth/3;
            }
        },
        // 点击二级分类标签
        changeSecondMoreList(index){
            console.log("-------------"+index)
            this.secondCateIndex = index;
            this.$refs.goodsListBox.scrollTop = 0;
            this.sortMethod = "";
            this.isPriceSort = false;
            // this.rows = 10;
            this.goodsListData= [];
            this.goodsFirstHttp = false;
            this.$store.commit("moreGoodsPageNumberChange", {
                custPageNum: true,
                pageNumber: 1
            });
            this.getMoreGoods(this.pageNumber);
        },
        /**
         * 获取二级分类列表
         * @param
         * cateParent Number  一级分类CODE
         */
        getGoodSecondCate(){
            let paramData = {
                cateParent:this.goodsCateList[this.goodsCateIndex].cateCode,
                areaCode: this.$userInfo.AREA_CODE,
                ownerCode: this.$userInfo.OWNER_CODE,
            }
            getGoodSecondCateService(paramData).then(res=>{
                this.secondCateFirstHttp = true;
                if (res.resultCode == 0) {
                    this.secondCateList = res.data;
                    this.secondCateIndex = 0;
                    // this.rows = 10;
                    this.$store.commit("moreGoodsPageNumberChange", {
                        custPageNum: true,
                        pageNumber: 1
                    });
                    this.getMoreGoods(this.pageNumber)
                }else
                {
                    this.secondCateList = [];
                }
            }).catch(error=>{
                this.secondCateFirstHttp = true;
            })
        },
        /**
         * 拉取选中分类的更多数据
         * @param
         * areaCode  Number      小区CODE
         * ownerCode Number  业主CODE
         * cateCode Number  分类code
         * page   Number     页数
         * rows  Number  个数
         * sortMethod Number 排序
         **/
        getMoreGoods(pageNumber, isRefresh = true){
            this.isloading = true;
            let paramData = {
                areaCode: this.$userInfo.AREA_CODE,
                ownerCode: this.$userInfo.OWNER_CODE,
                cateCode: this.secondCateList[this.secondCateIndex].cateCode,
                page: pageNumber,
                rows: this.rows,
                sortMethod:this.sortMethod
            }
            this.getGoodsForCate(paramData).then(res => {
                if(res.resultCode == 0){
                    if(res.data.list.length!=0){
                        res.data.list.map((item,index)=>{
                            if(index%2==0){
                                item.spuPicUrl = item.spuPicUrl.replace(/xwjfile.juhaolian.cn/,'xwjfilea.juhaolian.cn')
                            }
                            Vue.prototype.$isHasLocalImage(item.spuPicUrl,function(localImgUrl){
                                item.spuPicUrl = localImgUrl;
                            })
                        })
                    }
                    if (!isRefresh) {
                        this.goodsListData = this.goodsListData.concat(res.data.list);
                    } else {
                        this.goodsFirstHttp = true;
                        this.goodsListData = res.data.list;
                    }
                    this.loading = false;
                    this.isloading = false;
                    this.finished = !res.data.hasNextPage;
                }else
                {
                    this.goodsListData = [];
                    this.goodsFirstHttp = true;
                }
            }).catch(error => {
                this.goodsListData = [];
                this.goodsFirstHttp = true;
                this.loading = false;
                this.finished = false;
            });
        },
        getNewGoodsListData(){
            let paramData = {
                areaCode: this.$userInfo.AREA_CODE,
                ownerCode: this.$userInfo.OWNER_CODE,
                cateCode: this.secondCateList[this.secondCateIndex].cateCode,
                page: 1,
                rows: this.changeInfoRows,
                sortMethod:this.sortMethod
            }
            this.getGoodsForNewCate(paramData).then(res => {
                if(res.resultCode == 0){
                    this.goodsListData.map((item,index)=>{
                        res.data.list.map((resItem)=>{
                            if(item.spuCode == resItem.spuCode){
                                item.skuPrice = resItem.skuPrice;
                                item.cartCount = resItem.cartCount;
                                item.storeNumber = resItem.storeNumber;
                            }
                        })
                    })
                }
            }).catch(error => {
                this.loading = false;
                this.finished = false;
            });
        },
        //加载更多商品
        onLoad() {
            console.log("----------"+this.isloading)
            if(this.isloading){
                this.loading = false;
                return
            }
            this.getMoreGoods(this.pageNumber,false)
        },
        //价格排序
        priceSort(){
            if (!this.isPriceSort){
                this.sortMethod = 1;
            }else{
                this.sortMethod = 2;
            }
            this.isPriceSort = !this.isPriceSort;
        },
        //销量排序
        clickSaleBtn(){
            this.sortMethod = 3;
            this.isPriceSort= false;
        },
        /**
         * 添加购物车
         * @param
         * ownerCode Number  业主CODE
         * spuCode   Number  商品CODE
         * skuNo     Number  商品分类CODE
         * shopCode  Number  店铺CODE
         * skuCount  Number  商品数量
         * mark      Number  业主App标识
         */
        addGoods(spuItem,goodsIndex){
            // if(spuItem.storeNumber==0){
            //     Toast('该商品正在努力补货中哦')
            //     return;
            // }
            if(this.addGoodsLoading||this.isShowCircle){
                return;
            }
            this.addBtnIcon = this.$refs.addBtn[goodsIndex];
            this.isShowCircle = true;
            this.addGoodsLoading = true;
            let paramData = {
                ownerCode:this.$userInfo.OWNER_CODE,
                spuCode:spuItem.spuCode,
                skuNo:spuItem.skuNo,
                shopCode:spuItem.shopCode,
                activitySpuId:spuItem.activitySpuId,
                skuCount:1,
                mark:1,
            }
            addGoodsService(paramData).then(res=>{
                if (res.resultCode == 0) {
                    this.getSkuTypeNum(true);
                    spuItem.cartCount+=1;
                    
                }else if (res.resultCode == '20100' && res.data.result == '0') {
                    this.$toast('您所添加商品超过最大购买量了哦');
                }
                this.addGoodsLoading = false;
            }).catch(error=>{
                this.addGoodsLoading = false;
            })
        },
        /**
         * 查询购物车数量
         * ownerCode  Number  业主CODE
         */
        getSkuTypeNum(isAddGoods=false){
            let paramData = {
                ownerCode:this.$userInfo.OWNER_CODE
            }
            getSkuTypeNumService(paramData).then(res=>{
                if (res.resultCode == 0) {
                    if (isAddGoods){
                        Toast({
                            message: '添加购物车成功',
                            duration:1000
                        });
                    }
                    this.skuTypeNum = res.data.skuTypeNum;
                }
            }).catch(error=>{
            })
        },
        //返回上一页
        goToBack(){
            this.$router.go(-1);
        },
        //去搜索页面
        goToGoodsSearch(speechTxt){
            this.tabsScrollLeft = this.$refs.tabBarScroll.scrollLeft;
            this.goodsListTop = this.$refs.goodsListBox.scrollTop;
            this.classifyScrollTop = this.$refs.classifyScroll.scrollTop;
            this.$router.push({
                name: 'goodsSearch',
                params: {
                    speechTxt : speechTxt,
                    isRefresh : 1,
                    placeholderWord: this.searchKeyword
                }
            })
        },
        //去购物车页面
        goToShoppingCart(){
            this.tabsScrollLeft = this.$refs.tabBarScroll.scrollLeft;
            this.goodsListTop = this.$refs.goodsListBox.scrollTop;
            this.classifyScrollTop = this.$refs.classifyScroll.scrollTop;
            this.$router.push({
                name: 'shoppingCart',
                params: {

                }
            })
        },
        //去商品详情页
        goToProduct(goodsItem){
            this.tabsScrollLeft = this.$refs.tabBarScroll.scrollLeft;
            this.goodsListTop = this.$refs.goodsListBox.scrollTop;
            this.classifyScrollTop = this.$refs.classifyScroll.scrollTop;
            if(goodsItem.activitySpuId){
                this.$router.push({
                    name: 'flashSaleGoods',
                    query: {
                        spuCode:goodsItem.spuCode,
                        activitySpuId:goodsItem.activitySpuId,
                        goodsUrl:goodsItem.spuPicUrl
                    }
                })
            }else{
                this.$router.push({
                    name: 'goodsDetail',
                    params: {
                        spuCode:goodsItem.spuCode,
                        goodsUrl:goodsItem.spuPicUrl
                    }
                })
            }



        },
        clearData(){
            this.goodsCateList=[]; // 一级分类数据列表
            this.secondCateList=[]; // 二级分类数据列表
            this.secondCateIndex=0; // 二级分类下标
            this.goodsListData=[];//商品集合
            this.finished = false;
            this.sortMethod = "";
            this.isPriceSort = false;
            this.goodsFirstHttp =false;
            this.secondCateFirstHttp = false;
        },
        // 获取搜索关键词
        getSearchKeyword() {
            let reqData = {
                areaCode: this.$userInfo.AREA_CODE,
                fromType: 2
            }
            getSearchKeywordService(reqData).then(res=>{
                if (res.resultCode == 0) {
                    // this.searchKeyword = '雪糕';
                    this.searchKeyword = res.data;
                    this.defaultWord = '搜索好物';
                }
            }).catch(error=>{
                console.log(error);
                this.defaultWord = '搜索好物';
            })
        },
    },
}
